import React, { useState, useEffect } from "react";
import "./dashboard.scss";
import Intro from "../../views/intro/intro";
import About from "../../views/about/about";
import Resume from "../../views/resume/resume";
import Contact from "../../views/contact/contact";
import Footer from "../../views/footer/footer";
import { getData } from "../../services/firebase.service";
import { hideLoading, showButtonGoTop, showLoading } from "../../ui-script.js";
import Header from "../../views/header/header";

interface data {
  experience: Array<any>;
  education: Array<any>;
  personalData: object;
  skills: Array<any>;
}

function Dashboard() {
  let aux: data = {
    experience: [],
    education: [],
    personalData: {},
    skills: [],
  };
  const [mainData, setMainData] = useState<any>();

  useEffect(() => {
    getAllData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  async function getAllData() {
    showButtonGoTop();
    showLoading();
    let data = await getData();
    data.forEach((item: any) => {
      switch (item.label) {
        case "experience":
          aux.experience = item.data;
          break;
        case "education":
          aux.education = item.data;
          break;
        case "personal-data":
          aux.personalData = item;
          break;
        case "skills":
          aux.skills = item.habilidades;
          break;
        default:
          break;
      }
    });
    setMainData(aux);
    hideLoading();
  }

  return (
    <div id="top">
      <div id="preloader">
        <div id="loader"></div>
      </div>
      {/* Header */}
      <Header />
      {/* Intro Section */}
      {mainData && mainData.personalData && (
        <Intro personalData={mainData.personalData} />
      )}
      {/* About Section */}
      {mainData && mainData.personalData && mainData.skills && (
        <About personalData={mainData.personalData} skills={mainData.skills} />
      )}
      {/* Resume Section */}
      {mainData && mainData.experience && mainData.education && (
        <Resume
          experience={mainData.experience}
          education={mainData.education}
        />
      )}
      {/* Contact Section */}
      {mainData && mainData.personalData && (
        <Contact personalData={mainData.personalData} />
      )}
      {/* Footer Section */}
      <Footer />
    </div>
  );
}

export default Dashboard;
